@import url(https://fonts.googleapis.com/css2?family=Chango&display=swap);
html,
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  background-color: #000;
}

.title,
.panel-heading {
  font-family: Chango;
}

.custom-input-control.control.has-icons-left span.icon.is-left {
  top: 2px !important;

  color: #aaa;
}
.custom-input-control.control.has-icons-left {
  display: flex;
}

.dropdown-content .custom-input-control.control.has-icons-left {
  margin-right: 3px;
}
.as-link {
  cursor: pointer;
}
.as-link:hover {
  background-color: #fafafa;
  color: #3273dc;
}
.container {
  min-width: 100%;
}
.pointer {
  cursor: pointer;
}
.white {
  color: white;
}

.flex {
  display: flex;
}
.italic {
  font-style: italic;
}
.text_center {
  text-align: center !important;
}
.text_right {
  text-align: right !important;
}
.space-right {
  margin-right: 1em !important;
}
.space-left {
  margin-left: 1em !important;
}
.space-top {
  margin-top: 1em !important;
}
.space-bottom {
  margin-bottom: 1em !important;
}
.more-space-bottom {
  margin-bottom: 3em !important;
}

/*source : https://bulmatemplates.github.io/bulma-templates/templates/landing.html */

.app-hero.is-info {
  background: url(/static/media/martin-sattler-8peGuud5cEw-unsplash.a6c0bfc7.jpg) no-repeat
    center center fixed;
  background-size: cover;
}
.app-hero .nav,
.app-hero.is-success .nav {
  box-shadow: none;
}
.app-hero .subtitle {
  padding: 2rem 0;
  line-height: 1.5;
}

.app-box {
  min-height: 1000px;
  padding: 0;
  background-color: white;
  border-radius: 6px;
}
@media only screen and (max-width: 450px) {
  .app-hero-body {
    padding: 0;
    margin: 0;
  }
}

.AppNavbar_Navbar__3TBvG {
  padding-left:2rem;
  padding-right: 2rem;
  padding-top: 1rem;;
}
.AppNavbar_NavbarMenu__1qLDQ {
  background-color: transparent !important;
}

